// import React, { useEffect, useState, useRef, useCallback } from "react";
import axios from "axios";
import { Context as FormContext } from "../../context/FormContext";
import { Context as DatesContext } from "../../context/DatesContext";
import { useContext } from "react";
// import "./FormStyles.css";
import { areIntervalsOverlapping } from "date-fns";
import React, { useEffect, useState } from "react";
import {
  add,
  differenceInDays,
  isBefore,
  isAfter,
  isSameDay,
  format,
  addDays,
} from "date-fns";
import MenuItem from "@mui/material/MenuItem";
import { InputForm } from "./NewForm";
import { useForm, Controller } from "react-hook-form";
import "./FormGridStyles.css";
import CloseIcon from "@mui/icons-material/Close"; // Import CloseIcon from Material-UI icons
import { is } from "date-fns/locale";

export const NewFormm = () => {
  const handleCloseForm = () => {
    showFormMsg(false); // Close the form
  };
  // you can also target specific fields by their names
  const {
    state: {
      calendar: { intervalRentedArray },
    },
  } = useContext(DatesContext);
  const { showFormMsg } = useContext(FormContext);
  const { intervalRented, userId } = useContext(FormContext).state;

  const { start, end, room, formType } = intervalRented;

  const startDate = new Date(start);
  const formattedStartDate = startDate.toISOString().split("T")[0];
  const endDate = new Date(end);
  const formattedEndDate = endDate.toISOString().split("T")[0];
  const date = new Date("1970-01-01T00:00:00.009Z");
  const formattedTime = format(date, "HH:mm");
  console.log(formType);
  const {
    handleSubmit,
    control,
    watch,
    setValue,
    setError,
    clearErrors,
    formState: { errors },
  } = useForm({
    defaultValues: {
      checkIn: formattedStartDate,
      checkOut: formattedEndDate,
      phone: "3487556765",
      nights: differenceInDays(endDate, startDate),
      roomNumber: room,
      name: "gerardo",
      email: "gon@gmail.com",
      // roomtype: "doble",
      price: 45000,
      precioTotal: 45000,
      comments: "what?",
      adelanto: 0,
      montoPendiente: 0,
      dni: 24916296,
      nombre_recepcionista: "nadie por ahi?",
    },
  });

  const [
    adelanto,
    checkIn,
    checkOut,
    comments,
    dni,
    email,
    montoPendiente,
    name,
    nights,
    nombre_recepcionista,
    phone,
    precioTotal,
    price,
    roomNumber,
    time,
    paymentMethod,
  ] = watch([
    "adelanto",
    "checkIn",
    "checkOut",
    "comments",
    "dni",
    "email",
    "montoPendiente",
    "name",
    "nights",
    "nombre_recepcionista",
    "phone",
    "precioTotal",
    "price",
    "roomNumber",
    "time",
    "paymentMethod",
  ]);

  const handleEmailChange = (newValue) => {
    const emailRegex = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i;
    setValue("email", newValue);
    if (!emailRegex.test(newValue)) {
      setError("email", {
        type: "pattern",
        message: "Invalid email address",
      });
    } else {
      clearErrors("email");
    }
  };

  const handleTimeChange = (newValue) => {
    setValue("time", newValue);
  };
  const handlePaymentMethodChange = (value) => {
    setValue("paymentMethod", value);
  };
  // Handle change function for the "nombre_recepcionista" field
  const handleNombreRecepcionistaChange = (newValue) => {
    setValue("nombre_recepcionista", newValue);
  };

  const handleCheckInChange = (value) => {
    setValue("checkIn", value);
    const newNights = differenceInDays(
      new Date(watch("checkOut")),
      new Date(value)
    );
    console.log(value, newNights);
    setValue("nights", newNights);

    // setEndNights({ ...endNights, checkIn: value, active: false });
  };

  const handleCheckOutChange = (value) => {
    setValue("checkOut", value);
    // Calculate new nights based on the new checkOut value and the current checkIn value
    const newNights = differenceInDays(
      new Date(value),
      new Date(watch("checkIn"))
    );
    setValue("nights", newNights); // Update the nights based on the new checkOut date
    // setEndNights({ ...endNights, checkOut: value, active: false });
    console.log(value, newNights);
  };
  const handleNightsChange = (nights) => {
    const nightsValue = parseInt(nights, 10); // Ensure the value is treated as a number
    if (nightsValue < 1) {
      setError("nights", {
        type: "valueError",
        message: "Number of nights must be at least 1",
      });
    } else {
      clearErrors("nights"); // Clear the error if the value is valid
      const newEndDate = add(new Date(watch("checkIn")), { days: nightsValue });
      const formattedEndDate = newEndDate.toISOString().split("T")[0];

      setValue("checkOut", formattedEndDate);
    }
  };
  const handlePrecioTotalChange = (newPrecioTotal) => {
    const precioTotalValue = parseFloat(newPrecioTotal);
    if (precioTotalValue < 0) {
      setError("precioTotal", {
        type: "valueError",
        message: "Total price must be non-negative",
      });
    } else {
      clearErrors("precioTotal");
      setValue("precioTotal", precioTotalValue);
    }
  };

  const handleadelantoChange = (value) => {
    const adelanto = parseFloat(value || 0);
    const total = parseFloat(watch("precioTotal") || 0);
    const montoPendiente = total - adelanto;
    setValue("adelanto", adelanto); // Actualizar el valor de adelanto
    setValue("montoPendiente", montoPendiente);
  };
  const handleRoomChange = (value) => {
    const roomNumber = parseInt(value, 10); // Convert value to an integer
    if (roomNumber < 1 || roomNumber > 15) {
      setError("roomNumber", {
        type: "rangeError",
        message: "Room number must be between 1 and 15.",
      });
    } else {
      clearErrors("roomNumber");
      setValue("roomNumber", value); // Only set the value if it's within the valid range
    }
  };

  // Place this useEffect in both EditForm and NewFormm components
  useEffect(() => {
    const nights = parseInt(watch("nights"), 10);
    const price = parseFloat(watch("price"));
    const precioTotal = parseFloat(watch("precioTotal"));

    if (!isNaN(nights) && !isNaN(price)) {
      // Update precioTotal when nights or price changes
      const newPrecioTotal = nights * price;
      setValue("precioTotal", newPrecioTotal);
    } else if (!isNaN(nights) && !isNaN(precioTotal)) {
      // Update price when nights or precioTotal changes
      const newPrice = precioTotal / nights;
      setValue("price", newPrice);
    }

    // Recalculate montoPendiente
    const adelanto = parseFloat(watch("adelanto") || 0);
    const newMontoPendiente = (precioTotal || 0) - adelanto;
    setValue("montoPendiente", newMontoPendiente);
  }, [
    watch("nights"),
    watch("price"),
    watch("precioTotal"),
    watch("adelanto"),
    setValue,
  ]);
  useEffect(() => {
    const roomNumber = watch("roomNumber");

    // Example validation: Check if roomNumber is within a specific range
    if (roomNumber < 1 || roomNumber > 100) {
      // Assuming rooms are numbered 1-100
      setError("roomNumber", {
        type: "rangeError",
        message: "Room number must be between 1 and 15.",
      });
    } else {
      clearErrors("roomNumber");
    }
  }, [watch("roomNumber"), setError, clearErrors]);

  useEffect(() => {
    const checkInDate = new Date(watch("checkIn"));
    const checkOutDate = new Date(watch("checkOut"));
    const nights = parseInt(watch("nights"), 10);
    const expectedCheckOutDate = add(checkInDate, { days: nights });

    if (
      isAfter(checkOutDate, checkInDate) &&
      isSameDay(expectedCheckOutDate, checkOutDate)
    ) {
      // Clear errors if the conditions are met
      clearErrors(["checkIn", "checkOut", "nights"]);
    } else {
      // Set specific error messages for each case
      if (!isAfter(checkOutDate, checkInDate)) {
        setError("checkIn", {
          type: "dateRange",
          message: "Check-in date must be before check-out date.",
        });
      }
      if (!isSameDay(expectedCheckOutDate, checkOutDate)) {
        setError("nights", {
          type: "dateMismatch",
          message: "Number of nights does not match the dates selected.",
        });
      }
    }
  }, [
    watch("checkIn"),
    watch("checkOut"),
    watch("nights"),
    setError,
    clearErrors,
  ]);
  const handleCommentsChange = (value) => {
    setValue("comments", value);
  };
  useEffect(() => {
    const currentComments = watch("comments");

    if (currentComments.length > 500) {
      setError("comments", {
        type: "maxLength",
        message: "Comment is too long. Maximum length is 500 characters.",
      });
    } else {
      clearErrors("comments");
    }
  }, [watch("comments"), setError, clearErrors]);

  const formatPhoneNumber = (input) => {
    // Remove all non-digit characters
    const digits = input.replace(/\D/g, "");

    // Format for Argentinian local number (3487550568)
    if (digits.length <= 4) {
      return digits;
    } else if (digits.length <= 7) {
      return `${digits.slice(0, 4)}-${digits.slice(4)}`;
    } else if (digits.length <= 10) {
      return `${digits.slice(0, 4)}-${digits.slice(4, 7)}-${digits.slice(7)}`;
    } else {
      // For international numbers
      return `+${digits.slice(0, digits.length - 10)}-${digits.slice(
        -10,
        -6
      )}-${digits.slice(-6, -3)}-${digits.slice(-3)}`;
    }
  };

  const validatePhoneNumber = (phoneNumber) => {
    // Remove all non-digit characters for validation
    const digits = phoneNumber.replace(/\D/g, "");

    // Valid if it's 10 digits (local format) or more (for international numbers)
    return digits.length >= 10;
  };

  const handlePhoneChange = (value) => {
    const formattedValue = formatPhoneNumber(value);
    setValue("phone", formattedValue);

    if (!validatePhoneNumber(formattedValue)) {
      setError("phone", {
        type: "validation",
        message:
          "Por favor, ingrese un nmero de telfono vlido de al menos 10 dgitos",
      });
    } else {
      clearErrors("phone");
    }
  };
  const onSubmit = async (data) => {
    const {
      checkIn,
      checkOut,
      nights,
      name,
      email,
      phone,
      roomNumber,
      paymentMethod,
    } = data;
    const nightsDifference = differenceInDays(
      new Date(checkOut),
      new Date(checkIn)
    );

    if (nightsDifference !== parseInt(nights, 10)) {
      setError("nights", {
        type: "dateMismatch",
        message: "Number of nights does not match the dates selected.",
      });
      return;
    }

    const isOverlapping = intervalRentedArray.some((interval) => {
      if (
        areIntervalsOverlapping(
          {
            start: new Date(checkIn),
            end: new Date(checkOut),
          },
          { start: new Date(interval.end), end: new Date(interval.start) }
        ) &&
        `${interval.room}` === `${watch("roomNumber")}` &&
        !isSameDay(new Date(checkIn), new Date(checkOut)) // Add this condition to exclude same day overlap
      ) {
        console.log(checkIn, checkOut, interval.end, interval.start);
      }
      const overlap =
        areIntervalsOverlapping(
          {
            start: new Date(checkIn),
            end: new Date(checkOut),
          },
          { start: new Date(interval.end), end: new Date(interval.start) }
        ) &&
        `${interval.room}` === `${watch("roomNumber")}` &&
        !isSameDay(new Date(checkIn), new Date(checkOut)) && // Exclude overlap when check-in overlaps with existing check-out
        !isSameDay(new Date(checkIn), new Date(interval.start)); // Additional check to exclude overlap when check-in overlaps with existing check-out
    });
    console.log(isOverlapping);

    if (!isOverlapping) {
      console.log(intervalRented);
      let convertedCheckin = checkIn.split("-").join("/");
      let convertedChecOut = checkOut.split("-").join("/");

      // const requestUrl = `http://localhost:8000/create-reservation`;
      const requestUrl = `/create-reservation`;

      try {
        const res = await axios({
          method: "post",
          url: requestUrl,
          data: {
            name,
            email,
            phone,
            userId,
            room: `${roomNumber}`,
            start: convertedCheckin,
            end: convertedChecOut,
            isOverlapping: "false",
            price,
            nights,
            comments,
            precioTotal,
            adelanto,
            time: formattedTime,
            nombre_recepcionista,
            montoPendiente,
            dni,
            comments,
            paymentMethod,
          },
        });
      } catch (error) {
        console.error(error); // Handle error response
      }
    } else {
      // Handle the case where there is an overlapping reservation
      setError("checkIn", {
        type: "overlap",
        message: "The selected dates overlap with an existing reservation.",
      });
    }
    showFormMsg(false);
  };

  return (
    <form onSubmit={handleSubmit(onSubmit)} className="formContainer">
      <button
        onClick={() => showFormMsg(false)}
        style={{
          position: "absolute",
          top: "20px",
          right: "20px",
          border: "none",
          background: "orange",
        }}
        aria-label="Close form"
      >
        <CloseIcon />
      </button>
      <div>
        <InputForm
          control={control}
          name="name"
          type="text"
          className="personalInput"
          label="Nombre"
          value={watch("name")}
          handleChange={() => {}}
        />

        <InputForm
          control={control}
          name="email"
          type="email"
          label="Email"
          className="personalInput"
          value={watch("email")}
          rules={{
            required: "Email is required",
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: "Invalid email address",
            },
          }}
          handleChange={handleEmailChange}
        />
        <InputForm
          control={control}
          name="phone"
          type="tel"
          label="Telefono"
          className="personalInput"
          value={watch("phone")}
          handleChange={handlePhoneChange}
          rules={{
            validate: validatePhoneNumber,
          }}
        />
        <InputForm
          control={control}
          name="dni"
          className="personalInput"
          type="number"
          label="Dni"
          value={watch("dni")}
          handleChange={() => {}}
        />
      </div>
      <div>
        <InputForm
          control={control}
          name="roomNumber"
          type="number"
          // select={true}
          className=""
          label="Nro de Habitacion"
          value={watch("roomNumber")}
          options={Array.from({ length: 15 }, (_, i) => ({
            value: i + 1,
            label: i + 1,
          }))}
          rules={{
            required: "Room number is required",
            min: { value: 1, message: "Invalid room number" },
            max: { value: 15, message: "Invalid room number" },
          }}
          handleChange={handleRoomChange}
        />

        <InputForm
          control={control}
          name="nights"
          type="number"
          style={{ gridColumn: 2, gridRow: 2 }}
          label="Numero de Noches"
          className=""
          value={watch("nights")}
          rules={{
            min: { value: 1, message: "Number of nights must be at least 1" },
          }}
          handleChange={handleNightsChange}
        />

        <InputForm
          control={control}
          name="checkIn"
          className="dateInput"
          type="date"
          label="Check-In"
          value={watch("checkIn")}
          handleChange={handleCheckInChange}
        />
        <InputForm
          control={control}
          name="checkOut"
          type="date"
          className=""
          label="Check-Out"
          value={watch("checkOut")}
          handleChange={handleCheckOutChange}
        />
      </div>

      <div>
        <InputForm
          control={control}
          name="price"
          type="number"
          className="priceInputs"
          label="Precio"
          value={watch("price")}
          // rules={{
          //   required: "Price is required",
          //   min: { value: 0, message: "Price must be greater than 0" },
          // }}
          handleChange={() => {}}
        />
        <InputForm
          control={control}
          name="precioTotal"
          type="number"
          className=""
          label="PrecioTotal"
          value={watch("precioTotal")}
          // rules={{
          //   required: "PrecioTotal is required",
          //   min: { value: 0, message: "Price must be greater than 0" },
          // }}
          handleChange={handlePrecioTotalChange}
        />
        <InputForm
          control={control}
          name="montoPendiente"
          type="number"
          className=""
          label="Monto Pendiente"
          value={watch("montoPendiente")}
          readOnly
          handleChange={() => {}}
        />
        <InputForm
          control={control}
          name="adelanto"
          type="number"
          className=""
          label="Adelanto"
          value={watch("adelanto")}
          handleChange={handleadelantoChange}
        />
        <InputForm
          control={control}
          name="paymentMethod"
          type="select"
          className=""
          label="Mtodo de Pago"
          value={watch("paymentMethod")}
          handleChange={handlePaymentMethodChange}
          options={[
            { value: "efectivo", label: "Efectivo" },
            { value: "tarjeta", label: "Tarjeta" },
            { value: "deposito", label: "Depsito" },
          ]}
        />
      </div>
      <InputForm
        control={control}
        name="comments"
        multiline={true}
        rows={3}
        type="textarea"
        label="Comentarios"
        className=""
        value={watch("comments")}
        rules={{
          maxLength: {
            value: 500,
            message: "Comentario muy largo, abreviar por favor",
          },
        }}
        handleChange={handleCommentsChange}
        // Set this to occupy the second column
      />
      <InputForm
        control={control}
        name="nombre_recepcionista"
        type="text"
        className="commentsStyles"
        label="Nombre Recepcionista"
        value={watch("nombre_recepcionista")}
        handleChange={handleNombreRecepcionistaChange}
      />

      <input
        type="submit"
        style={{
          background: "green",
          color: "white",
          border: "none",
          padding: "10px 20px",
          cursor: "pointer",
          width: "50%",
          height: "50%",
        }}
        value="Ok"
      />
    </form>
  );
};
