import React from "react";
import styled from "@emotion/styled";
import { keyframes } from "@emotion/react";

const fadeIn = keyframes`
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  // max-width: 300px;
  max-height: 400px;
  gap: 10px;
  padding: 15px;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  animation: ${fadeIn} 0.3s ease-out;
  overflow-y: auto;
`;

const MainSection = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
`;

const Column = styled.div`
  flex: 1;
  min-width: calc(25% - 10px);
  display: flex;
  flex-direction: column;
  gap: 5px;
`;

const Section = styled.div`
  display: flex;
  flex-direction: column;
  gap: 3px;
`;

const SectionTitle = styled.h2`
  font-size: 0.85em;
  color: #333;
  margin: 0;
  padding-bottom: 2px;
  border-bottom: 2px solid #3498db;
`;

const InfoItem = styled.p`
  margin: 1px 0;
  font-size: 0.75em;
  color: #555;
`;

const InfoLabel = styled.span`
  font-weight: bold;
  color: #3498db;
`;

export const DescDetail = ({
  name,
  phone,
  email,
  end,
  start,
  room,
  adelanto,
  comments,
  dni,
  montoPendiente,
  nights,
  id,
  nombre_recepcionista,
  time,
  precioTotal,
  price,
}) => {
  return (
    <Container>
      <MainSection>
        <Column>
          <Section>
            <SectionTitle>Personal</SectionTitle>
            <InfoItem>
              <InfoLabel>Nombre:</InfoLabel> {name}
            </InfoItem>
            <InfoItem>
              <InfoLabel>Telefono:</InfoLabel> {phone}
            </InfoItem>
            <InfoItem>
              <InfoLabel>Email:</InfoLabel> {email}
            </InfoItem>
            <InfoItem>
              <InfoLabel>DNI:</InfoLabel> {dni}
            </InfoItem>
          </Section>
        </Column>
        <Column>
          <Section>
            <SectionTitle>Check-in</SectionTitle>
            <InfoItem>
              <InfoLabel>Fecha:</InfoLabel> {start}
            </InfoItem>
            <InfoItem>
              <InfoLabel>Time:</InfoLabel> {time}
            </InfoItem>
          </Section>
        </Column>
        <Column>
          <Section>
            <SectionTitle>Check-out</SectionTitle>
            <InfoItem>
              <InfoLabel>Fecha:</InfoLabel> {end}
            </InfoItem>
            <InfoItem>
              <InfoLabel>Numero de Noches:</InfoLabel> {nights}
            </InfoItem>
          </Section>
        </Column>
        <Column>
          <Section>
            <SectionTitle>Habitacion</SectionTitle>
            <InfoItem>
              <InfoLabel>Numero:</InfoLabel> {room}
            </InfoItem>
          </Section>
        </Column>
      </MainSection>

      <MainSection>
        <Column>
          <Section>
            <SectionTitle>Pagos</SectionTitle>
            <InfoItem>
              <InfoLabel>Precio/Noche:</InfoLabel> {price}
            </InfoItem>
            <InfoItem>
              <InfoLabel>Total:</InfoLabel> {precioTotal}
            </InfoItem>
            <InfoItem>
              <InfoLabel>Adelanto:</InfoLabel> {adelanto}
            </InfoItem>
            <InfoItem>
              <InfoLabel>Pendiente:</InfoLabel> {montoPendiente}
            </InfoItem>
          </Section>
        </Column>
        <Column>
          <Section>
            <SectionTitle>Reserva</SectionTitle>

            <InfoItem>
              <InfoLabel>Receptionista:</InfoLabel> {nombre_recepcionista}
            </InfoItem>
          </Section>
        </Column>
        <Column>
          <Section>
            <SectionTitle>Comentarios</SectionTitle>
            <InfoItem>{comments}</InfoItem>
          </Section>
        </Column>
      </MainSection>
    </Container>
  );
};
