import React, { useContext, useEffect, useRef } from "react";
import styled from "@emotion/styled";
import { keyframes } from "@emotion/react";
import { Context as IdContext } from "../../context/IdContext";
import { DescDetail } from "./DescDetail";

const fadeIn = keyframes`
  from { opacity: 0; transform: scale(0.95); }
  to { opacity: 1; transform: scale(1); }
`;

const DescriptionContainer = styled.div`
  position: fixed;
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 12px;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
  padding: 20px;
  width: 60%;
  max-width: 500px;
  max-height: 80vh;
  overflow-y: auto;
  z-index: 20000;
  animation: ${fadeIn} 0.3s ease-out;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }

  &::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

  @media (max-width: 768px) {
    width: 90%;
    max-height: 90vh;
  }
`;

export const RentedDescription = ({ pos }) => {
  const containerRef = useRef(null);
  const {
    state: { id },
  } = useContext(IdContext);

  // useEffect(() => {
  //   if (containerRef.current) {
  //     const { width, height } = containerRef.current.getBoundingClientRect();
  //     let newX = pos.x + 10;
  //     let newY = pos.y + 10;

  //     if (newX + width > window.innerWidth - 10) {
  //       newX = pos.x - width - 10;
  //     }
  //     if (newY + height > window.innerHeight - 10) {
  //       newY = pos.y - height - 10;
  //     }

  //     containerRef.current.style.left = `${newX}px`;
  //     containerRef.current.style.top = `${newY}px`;
  //   }
  // }, [pos]);
  const containerStyle = {
    left: `${pos.x > window.innerWidth / 2 ? pos.x - 400 : pos.x + 5}px`,
    top: `${pos.y > window.innerHeight / 2 ? pos.y - 350 : pos.y + 5}px`,
  };

  return (
    <DescriptionContainer style={containerStyle}>
      <DescDetail
        name={id.name}
        phone={id.phone}
        email={id.email}
        end={id.end}
        start={id.start}
        room={id.room}
        adelanto={id.adelanto}
        comments={id.comments}
        dni={id.dni}
        montoPendiente={id.montoPendiente}
        nights={id.nights}
        id={id.id}
        nombre_recepcionista={id.nombre_recepcionista}
        time={id.time}
        precioTotal={id.precioTotal}
        price={id.price}
      />
    </DescriptionContainer>
  );
};
